import React, { useState, useEffect } from 'react';
import { WrapperForm } from "./Styles"
import { Input, InputNumber, message, Avatar, Spin } from "antd";
import { SimpleForm } from '../com/form/SimpleForm';
import { translate } from 'react-polyglot';
import { useSelector, useDispatch } from "react-redux";
import { S3Button } from "../com/form/";
import { URL_AVATAR, URL_S3 } from '../../constants';
import { getService } from "../../services/";
import { CHANGE_ENTITY } from '../../constants/actionTypes';




const FormProfile = ({ t, reference = "users", ...props }) => {
    const dispatch = useDispatch();
    const [record, setRecord] = useState();
    const [loading, setLoading] = useState(true);
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const user = useSelector(({ auth }) => {
        return auth.user;
    });
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    })

    const getData = () => {
        if (reference) {
            const service = getService(reference);
            service.get(user.id)
                .then(user => {
                    setRecord(user);
                    setLoading(false);

                })
                .catch(err => {
                    setLoading(false);
                    message.error(err.message);
                });
        }

    }
    const handleOnUploaded = (field, url) => {
        /*  setImage(url); */
        const service = getService(reference);
        service.patch(record.id, {
            [field]: url
        })
            .then(response => {
                setRecord({
                    ...record,
                    [field]: url,

                })
                message.info("Foto actualizada!");
            })
            .catch(err => message.error(err.message));
    };
    useEffect(() => {
        getData();
    }, [user]);
    if (loading) return <Spin size="large" />;
    return (
        <WrapperForm configuration={{
            primary_color: entity && entity.primary_color || "var(--color-primary);",
            secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
            text_color: entity && entity.text_color || "var( --color-gray-dark)"
        }}>
            <SimpleForm title={<h1>{t("formProfile.titletext")}</h1>}
                className="form-piker"
                source={"users"}
                id={record.id}
                record={record}
                textAcceptButton={t("formProfile.button")}
            >
                <S3Button
                    className={"container-s3Button"}
                    flex={1}
                    size="large"
                    source="avatar_path"
                    name="avatar_path"
                    record={record}
                    idComponent="btn-picture"
                    onUploaded={handleOnUploaded}
                    path="users"
                    finalPath="logo"
                    id={record.id}
                />
                <Input
                    flex={1}
                    name="first_name"
                    placeholder={t("formNamePlaceholderText")}
                    label={t("formNamePlaceholderText")}
                />
                <Input
                    flex={1}
                    name="last_name"
                    placeholder={t("formLastPlaceholderText")}
                    label={t("formLastPlaceholderText")}
                />
                <Input
                    flex={1}
                    disabled
                    name="email"
                    placeholder={t("form.emailPlaceholder")}
                    label={t("form.emailPlaceholder")}
                />

                <Input
                    flex={1}
                    name="phone"
                    placeholder={t("formPhonePlaceholderText")}
                    label={t("formPhonePlaceholderText")}
                />


            </SimpleForm>
        </WrapperForm>
    );
};
export default translate()(FormProfile);