import React from 'react';
import { Wrapper } from './Styles'
import { Input, Row, Col } from "antd"
import { translate } from 'react-polyglot';
import { useSelector } from 'react-redux'
import FormProfile from '../../components/form-hotel/FormProfile';
import LayoutCanter from '../../layouts/layout_center';



const MyAccount = ({ t, props }) => {
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    })
    return (
        <Wrapper>
            <LayoutCanter /* background={"/images/bg1.jpg"} */>
                <Row className="section section-container" justify="center" type="flex" align="center" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
                    <Col className="container-section" xl={12} lg={24} md={24} xs={24}>
                        <FormProfile />
                    </Col>
                </Row>
            </LayoutCanter>
        </Wrapper>
    );
};
export default translate()(MyAccount);